<script>
export default {
    props: ['teacher', 'countries'],

    data: () => ({
        form: {
            teacher: {
                birthday: [],
            },
            address: {},
            levels: [],
            categories: [],
        },

        loading: false,
        errors: new ErrorBag(),
    }),

    created() {
        const { teacher, form } = this

        form.teacher.first_name = teacher.first_name
        form.teacher.last_name = teacher.last_name

        const birthday = new Date(teacher.birthday)

        form.teacher.birthday = [
            birthday.getDate() + 1,
            birthday.getMonth() + 1,
            birthday.getFullYear(),
        ]

        form.teacher.country_id = teacher.country_id

        // Address
        const { address } = teacher
        form.address.country_id = address.country_id
        form.address.city = address.city
        form.address.state = address.state
        form.address.line = address.line
    },

    methods: {
        handleSubmit() {
            if (this.loading) return

            this.loading = true
            this.errros.clear()

            try {
            } catch (error) {
                console.log(error.response || error)
                if (error.response) {
                    window.scrollTo({ top: 0, behavior: 'smooth' })
                    const { errors } = error.response.data
                    this.errors.set(errors || {})
                }
            } finally {
